@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat,sans-serif;
  background-color:#220b2e;
  color: white;
}

.coin-app{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top:64px;
  color:white;
}

.coin-search{
  margin-bottom:64px;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.coin-text{
  margin-bottom:20px;
  text-align: center;
}

.coin-input{
  padding: 0 30px 0px 30px;
  width:300px;
  height: 50px;
  border-radius:5px;
  border:solid 1px #ffffff;
  /*background-image: linear-gradient(-225deg,#ac32e4 0%,#7918f2 48%, #4801ff 100%);*/
  color: #e2e2e2;
}

.coin-input ::-webkit-input-placeholder{
  color: #e2e2e2;
}

.coin-input :-ms-input-placeholder{
  color: #e2e2e2;
}

.coin-input ::placeholder{
  color: #e2e2e2;
}

.coin-input :focus{
  outline-style:none;
}

.coin-input::-webkit-search-cancel-button{
  background:white;   
}

.coin-table{
  width: 80%;
}

th,td{
  height:100px;
  text-align:center;
  vertical-align: middle;
  border-bottom: solid 1px #e2e2e2;
}

td p{
  text-align:center;
}
:root{
    --red:#ff0f0f;
    --green:#16bf0a;
}

.coin-container{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:center;
            justify-content:center;
}

img{
    height:30px;
    width: 30px;
    margin-right: 20px;
}

.red{
    color:#ff0f0f;
    color:var(--red)
}

.green{
    color:#16bf0a;
    color:var(--green)
}

.coin-name{
    font-size:20px;
    font-weight:bold;
}
